// If you don't want to use TypeScript you can delete this file!
import React from "react"
import { PageProps, Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

type DataProps = {
  site: {
    buildTime: string
  }
}

const UsingTypescript: React.FC<PageProps<DataProps>> = ({ data, path }) => (
  <Layout>
    <SEO title="Using TypeScript" />
    <h1>Поддержка TypeScript!</h1>
    <p>Это означает, что вы можете создавать и писать <em>.ts/.tsx</em> скрипты, компоненты и др. Однако следует заметить, что скрипты <em>gatsby-*.js</em> (например, gatsby-node.js) пока не поддерживают TypeScript.</p>
    <p>Для тестирования <em>typescript</em> необходимо используя npm выполнить <em>tsc --init</em> чтобы создать файл конфигурации <em>.tsconfig</em>.</p>
    <p>Текущая страница "{path}" создана на TypeScript {data.site.buildTime}.</p>
    <p>Для дальнейшего знакомства обратитесь к документации: <a href="https://www.gatsbyjs.com/docs/typescript/"></a>.</p>
    <Link to="/">Назад</Link>
  </Layout>
)

export default UsingTypescript

export const query = graphql`
  {
    site {
      buildTime(formatString: "YYYY-MM-DD hh:mm a z")
    }
  }
`
